html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

html {
  body {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color: hsl(222, 11%, 18%);
    color: white;
  }

    #main-logo {
      height: 250px;
      width: 250px;
      flex: none;
    }
    .center-links {
      width: 80%;
      max-width: 50em;
      height: 50px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      flex: none;
      margin-top: 2%;
      margin-bottom: 25%;
      a {
        text-decoration: none;
        color: inherit;
        font-weight: bold;
        font-size: 100%;
        height: 50px;
        width: 32%;
        margin: auto;
        line-height: 50px;
        text-align: center;
        box-sizing: border-box;
        border: 2px solid white;
      }
    }
  }
